body {
  margin: 0;
}

/*main {
  /*z-index:-1;
}*/

.page {
  height: 100vh;
  margin-left: 300px;
}

.navbar {
  display: flex;
  height: 100vh;
  width: 300px;
  position: fixed;
  background-color: #332f3f;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.navbar ul {
  list-style-type: none;
  color: white;
  font-size: 25px;
}

.navbar ul li {
  cursor: pointer;
  padding:10px;
}

.navbar li:hover {
  color: #0945b4;
}

.navbar ul li.active {
  font-weight: bold;
  color: #0945b4;
}

